<!--父子组件的关系可以总结为 prop 向下传递，事件向上传递。
父组件通过 prop 给子组件下发数据，
子组件通过事件给父组件发送消息。-->
<!--非父子组件之间传值，需要定义个公共的公共实例文件tem.js，作为中间仓库来传值，不然路由组件之间达不到传值的效果-->
<!--https://blog.csdn.net/lander_xiong/article/details/79018737-->
<template>
    <div>
        父组件0:
        <input type="text" v-model="name">
        <br>
        <br>
        <!-- 引入子组件 -->
        <child v-on:childByValue="updateParent" :inputName="name"></child>
        父组件1:
        <input type="text" v-model="name1" disabled="disabled">
    </div>
</template>
<script>
    import child from './child'
    export default {
        components: {
            child
        },
        data () {
            return {
                name: '传给子组件',
                name1:'我是父组件'
            }
        },
        methods: {
            updateParent: function (childValue) {
                // childValue就是子组件传过来的值
                // console.log(childValue);
                this.name1 = childValue;
            }
        }
    }
</script>


