<template>
    <el-container>
        <el-header style="height: 80px;">
            <div></div>
        </el-header>
        <el-container>
            <el-main>
                <div></div>
                <div></div>
            </el-main>
            <el-aside>
                <div></div>
            </el-aside>
        </el-container>
        <el-footer>
            <div></div>
        </el-footer>
    </el-container>
</template>

<script>
    export default {
        name: 'HelloWorld',
        data() {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-header, .el-footer {
        position: relative;
        padding: 10px;
        color: #333;
        text-align: center;
    }

    .el-aside {
        width: 200px;
        padding: 10px;
        text-align: center;
    }

    .el-main {
        padding: 10px;
        /*background-color: #E9EEF3;*/
        color: #333;
        text-align: center;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .el-header > div, .el-footer > div {
        width: 100%;
        height: 100%;
        background: #B3C0D1;
    }

    .el-main div {
        position: relative;
        width: 550px;
        height: 600px;
        margin-left: 0.1%;

        background: darkkhaki;
    }

    .el-aside div {
        position: relative;
        width: 100%;
        height: 100%;
        background: lightgreen;
    }
</style>



